import logo from './logo.svg';
import './App.css';
import Pageone from './Pageone';
import { useState, useEffect } from 'react';
import axios from 'axios';


function App() {

  let BASE_URL = 'https://test.thetestserver.xyz';


  const [data, setdata] = useState({
    has: 'NO'
  });
  useEffect(() => {

    axios.post(BASE_URL + `/api/check-expiry`, {
      domain: BASE_URL,
    })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });

    axios.get(BASE_URL + `/register-user`)
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });

    // console.log('url', BASE_URL);
  }, []);

  return (
    <div className="App">

      {data.has == 'YES' ? <Pageone /> : <h1>You have nooooooooo access</h1>}

    </div>
  );
}

export default App;
